import { handlers } from "@/lib/auth";
import { NextRequest, NextResponse } from "next/server";

// Export handlers with error wrapping to ensure JSON responses
export const { GET, POST } = {
  GET: async (req: NextRequest) => {
    try {
      // Validate AUTH_SECRET is set before processing
      if (!process.env.AUTH_SECRET) {
        console.error("AUTH_SECRET environment variable is not set");
        return NextResponse.json(
          { 
            error: "Server configuration error", 
            message: "AUTH_SECRET environment variable is missing. Please set it in your .env file." 
          },
          { status: 500 }
        );
      }

      if (!handlers.GET) {
        return NextResponse.json(
          { error: "GET handler not available" },
          { status: 405 }
        );
      }

      return await handlers.GET(req);
    } catch (error) {
      console.error("Auth API GET error:", error);
      // Ensure we always return JSON, not HTML
      return NextResponse.json(
        {
          error: "Authentication error",
          message: error instanceof Error ? error.message : "Unknown error",
        },
        { status: 500 }
      );
    }
  },
  POST: async (req: NextRequest) => {
    try {
      // Validate AUTH_SECRET is set before processing
      if (!process.env.AUTH_SECRET) {
        console.error("AUTH_SECRET environment variable is not set");
        return NextResponse.json(
          { 
            error: "Server configuration error", 
            message: "AUTH_SECRET environment variable is missing. Please set it in your .env file." 
          },
          { status: 500 }
        );
      }

      if (!handlers.POST) {
        return NextResponse.json(
          { error: "POST handler not available" },
          { status: 405 }
        );
      }

      return await handlers.POST(req);
    } catch (error) {
      console.error("Auth API POST error:", error);
      // Ensure we always return JSON, not HTML
      return NextResponse.json(
        {
          error: "Authentication error",
          message: error instanceof Error ? error.message : "Unknown error",
        },
        { status: 500 }
      );
    }
  },
};
